module.exports = [{"id":1,"title":"游戏启航班任务剪刀石头布游戏说明","datetime":"Dec 25, 2017 7:32:28 PM","content":"# 游戏启航班任务剪刀石头布游戏说明\n\n## 任务描述：\n\n实现一个简单的网页游戏，玩家可以在网页上通过点击，选择要出的是石头、剪刀或是布，电脑也会随机出一个，然后判断胜负，并记录获胜总次数。\n\n以上两图仅作参考，大家可以随意按照自己的喜欢进行游戏界面设计。\n\n## 任务要求：\n\n* 个人完成，团队内部交流，互相评价代码\n* 做完后在班级群里分享\n\n## 任务时间\n\n7月18日 至 7月26日\n\n## 学习资料\n\n* [教学视频](http://pan.baidu.com/s/1mgxh8XM)，密码: beag\n\n## HTML\n\n- [知乎上的一些Web基本概念介绍](http://www.zhihu.com/question/22689579)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n- [w3school html教程](http://w3school.com.cn/html/index.asp)\n- [MDN HTML](https://developer.mozilla.org/zh-CN/docs/Web/HTML/Introduction)\n\n## CSS\n\n- [MDN CSS](https://developer.mozilla.org/zh-CN/docs/Web/Guide/CSS/Getting_started)\n- [w3school css](http://w3school.com.cn/css/index.asp)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n\n## JavaScript\n\n- [MDN](https://developer.mozilla.org/zh-CN/docs/Web/JavaScript)\n- [慕课网JavaScript入门](http://www.imooc.com/learn/36)\n- [慕课网JavaScript教程](http://www.imooc.com/learn/10)\n- [w3school](http://www.w3school.com.cn/js/)\n- [Codecademy](http://www.codecademy.com/tracks/javascript)"},{"id":2,"title":"游戏启航班任务2048游戏说明","datetime":"Dec 25, 2017 7:31:36 PM","content":"# 游戏启航班任务2048游戏说明\n\n## 任务描述：\n\n实现一个简单的网页游戏，2048游戏，示例如下：\n\n以上两个仅作参考，大家可以随意按照自己的喜欢进行游戏界面设计。\n\n## 任务要求：\n\n* 团队协作完成\n* 做完后在班级群里分享\n* 使用HTML、CSS、JavaScript进行游戏的编写\n* 允许使用jQuery等类库、框架\n\n## 任务时间\n\n7月27日 至 8月14日\n\n## 学习资料\n\n## HTML\n\n- [知乎上的一些Web基本概念介绍](http://www.zhihu.com/question/22689579)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n- [w3school html教程](http://w3school.com.cn/html/index.asp)\n- [MDN HTML](https://developer.mozilla.org/zh-CN/docs/Web/HTML/Introduction)\n\n## CSS\n\n- [MDN CSS](https://developer.mozilla.org/zh-CN/docs/Web/Guide/CSS/Getting_started)\n- [w3school css](http://w3school.com.cn/css/index.asp)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n\n## JavaScript\n\n- [MDN](https://developer.mozilla.org/zh-CN/docs/Web/JavaScript)\n- [慕课网JavaScript入门](http://www.imooc.com/learn/36)\n- [慕课网JavaScript教程](http://www.imooc.com/learn/10)\n- [w3school](http://www.w3school.com.cn/js/)\n- [Codecademy](http://www.codecademy.com/tracks/javascript)\n"},{"id":3,"title":"游戏扬帆班任务「接元宝」游戏说明","datetime":"Dec 25, 2017 7:31:36 PM","content":"# 游戏扬帆班任务「接元宝」游戏说明\n\n## 任务描述\n\n实现一个移动端游戏，玩家通过触屏控制角色水平方向移动，接住从屏幕上方落下的元宝。记录玩家规定时间内接住的元宝个数作为一局的分数。\n\n## 任务要求\n\n## 任务时间\n\n7月27日 至 8月9日\n\n## 任务步骤\n\n### 1. 技术选型\n\n根据待开发游戏的类型和功能，从以下技术方案中挑选**一种**：\n\n- Canvas (推荐)\n    + **不使用游戏引擎**。参考资料：\n        - [Canvas API](https://developer.mozilla.org/zh-CN/docs/Web/API/Canvas_API)\n        - How to Make a Simple HTML5 Canvas Game [英文](http://www.lostdecadegames.com/how-to-make-a-simple-html5-canvas-game/) [中文](http://www.cnblogs.com/Wayou/p/how-to-make-a-simple-html5-canvas-game.html)\n    + **使用游戏引擎**。推荐 [Phaser](http://phaser.io/) 或 [CreateJS](http://createjs.com/)。HTML5 各游戏引擎的特性比较及开发者评价，可参考[这里](https://html5gameengine.com/)以供选择。参考资料：\n        - How to make a Flappy Bird in HTML5 [英文](http://www.gamedevacademy.org/how-to-make-a-flappy-bird-in-html5/) [中文](http://blog.jobbole.com/62203/)\n        - [Phaser 速查表](http://invrse.co/phaser-cheatsheet/)\n    \n- DOM + CSS\n    + 参考资料：\n        - [HTML5游戏开发实战分享](http://oklai.name/html5game/)\n        - [DOM + CSS3 实现小游戏 SwingCopters](http://www.cnblogs.com/lvdabao/p/3968464.html)\n\n- 两者结合：用 Canvas 绘制主体元素和动画，用 DOM 制作 UI。\n\n### 2. 一个适配移动端的游戏场景\n\n选择一种在移动端上的屏幕适配方案：\n\n- 方案一：固定宽高比，方便布置元素。\n    \n- 方案二：延伸至适应屏幕，原生既视感。\n\n### 3. 一个可操控的角色\n\n创建一个角色，实现一种操控方式：\n\n- 方案一：触击屏幕的任意位置，角色向该位置的对应水平点移动。\n    \n- 方案二：触摸屏幕左半边，角色向左移动；触摸屏幕右半边，角色向右移动\n\n- 方案三：触摸角色，拖拽移动。\n\n- 或者其它合理的触屏操控方案。\n\n### 4. 元宝\n\n- 实现元宝的随机掉落，包括起始水平位置的随机、时间点的随机。\n- 元宝回收机制：\n    + 即时创建，即时销毁。\n    + 对于频繁创建和销毁的对象，也可利用[对象池](https://zh.wikipedia.org/wiki/%E5%AF%B9%E8%B1%A1%E6%B1%A0%E6%A8%A1%E5%BC%8F)进行内存优化，算法具体实现可参考 [JavaScript Object Pool](http://blog.sklambert.com/javascript-object-pool/)。\n\n### 5. 碰撞检测\n\n- 选择一个高效合理的检测方法，判定角色是否接住元宝（自行实现算法或利用已有库皆可）。\n- 经过任务步骤4和5后，若游戏在移动端上的[帧率](https://zh.wikipedia.org/wiki/%E5%B8%A7%E7%8E%87)不在 50~60 fps 之间，建议进行优化使游戏运行流畅。\n\n### 6. 游戏界面元素\n\n游戏的核心玩法实现以后，陆续将界面元素及相应逻辑补充完整：\n\n- 开始按钮：触发首局游戏的开始；\n- 记分板：实时记录玩家一局的分数；\n- 倒计时器：限定一局的可玩时间，实时刷新；\n- 结束菜单：以浮层形式展现，拥有该局分数、最高分、重新开始等元素。\n\n### 7. 扩展\n\n想一想，怎样扩展可以使游戏更有趣呢？\n\n比如：\n\n- 掉炸弹：被砸中减分、减命或直接结束游戏；\n- 增加无尽模式：不限定时间，直至被炸弹砸中或者命消耗完；\n- 其它掉落物：或给予不同的分数，或改变角色的状态；\n- 角色不同状态下的贴图变化，比如接到元宝、被炸弹砸中；\n- 音效/音乐；\n- 元宝掉落初始方向不规则，甚至引入物理引擎；\n- 换个主题？接果子、接盘子、接妹子……任你想！\n- ……\n\n## 团队合作建议\n\n- 动手前，先共同讨论和设计游戏的具体流程、玩法及细节；\n- 队员分工可按组件（角色-元宝-界面元素-…），可按功能（适配-操控-回收机制-碰撞检测-…），也可按职能（主程-算法优化-设计-…）；\n- 每个队员都需要有代码提交；\n- 尝试进行组内的交叉 Code Review，尽量保证队伍内部代码风格一致。\n"},{"id":4,"title":"游戏扬帆班任务二「打飞机」","datetime":"Dec 25, 2017 7:31:36 PM","content":"#游戏扬帆班任务二「打飞机」\n\n## 任务描述\n\n实现一个移动端游戏，玩家通过手机左右倾斜来控制飞机的水平移动，射击不断出现的敌机，并躲避敌机的冲击。\n\n## 任务步骤\n\n### 1. 技术选型\n\n参见：[技术选型说明](https://github.com/baidu-ife/ife/blob/master/2015_summer/task/game_yangfan_01.md#1-技术选型)\n\n### 2. 游戏状态设计\n\n本游戏包含四个基本状态：\n\n- 欢迎状态：包含欢迎信息、开始游戏按钮、选项设置等；\n- 关卡状态：包含游戏的主体元素与核心玩法；\n- 暂停状态：通过关卡状态的暂停按钮触发，包含回到游戏按钮、重新开始按钮；\n- 结束状态：游戏关卡结束时触发，包含该局分数、最高记录、重新开始等元素；\n\n### 3. 背景\n\n- 利用「首尾相接」技巧，让一定高度的背景图片做反向循环滚动，造成主角向前行驶的视觉差；\n- 可以在背景的基础上添加更多的图片层，以不同的速度滚动，形成远近层次的效果；\n\n### 4. 主角\n\n本游戏主角即为我方飞机，它在飞行时会以帧动画的形式显示，例如下图的两帧构成飞机喷气的效果：\n\n可以增加更多帧使动画更加细腻流畅。\n\n### 5. 子弹\n\n- 初始位置：以所属飞机位置为基准；\n- 飞行效果：一旦发射，垂直方向移动，水平方向不变；\n- 回收机制：对于频繁创建和销毁的对象，可利用[对象池](https://zh.wikipedia.org/wiki/%E5%AF%B9%E8%B1%A1%E6%B1%A0%E6%A8%A1%E5%BC%8F)进行内存优化。\n\n### 6. 操控\n\n通过手机左右倾斜控制主角水平移动：\n\n- 利用 HTML5 提供的重力感应特性实现；\n- 可以考虑倾斜速率对主角移动速度的影响；\n- 尽量适配移动端多设备多浏览器；\n- 开发时可以使用 Chrome 的传感器模拟功能进行调试；\n\n参考资料：\n\n- [检测设备方向](https://developer.mozilla.org/zh-CN/docs/Web/API/Detecting_device_orientation)\n- [使用 HTML5 设备方向检测特性开发的重力小游戏](http://www.gbtags.com/gb/share/4247.htm)\n\n### 7. 敌机\n\n- 至少有两种类型的敌机，各类型之间在贴图、生命值、移动速度、出现频率、击落奖励等方面有所不同；\n- 随机初始位置和出现时间；\n- 数量随分数增加而增加，或设置阶段性阈值；\n- 选用合理的回收机制。\n\n\n### 8. 碰撞检测\n\n- 检测一：子弹与敌机的碰撞；\n- 检测二：敌机与主角的碰撞；\n- 主角、敌机的爆炸效果（帧动画或粒子效果）。\n\n### 9. 暂停与屏蔽\n\n暂停按钮触发暂停状态：\n\n- 屏蔽重力感应，屏蔽关卡状态的可点击元素；\n- 暂停主角、敌机、子弹的飞行，暂停背景滚动，暂停帧动画。\n\n### 10. 收尾\n\n- 关卡状态的其它界面元素，如记分板；\n- 其余游戏状态，即欢迎状态和结束状态，以及对应的转场逻辑。\n\n### 11. 扩展\n\n想一想，怎样扩展可以使游戏更有趣呢？\n\n比如：\n\n- 天降各种类型的道具，如改变子弹类型的、全屏攻击的、短时间内无敌的，等等；\n- 敌机也会发射子弹（考虑到操控的特殊性，应注意适当调试参数以控制游戏难度）；\n- BOSS 战，引入人工智能；\n- 子弹非自动发射，而是通过特定的操作；\n- 音效/音乐；\n- ……\n\n## 任务时间\n\n7月27日 至 8月14日"},{"id":5,"title":"移动启航班任务-个人移动名片","datetime":"Dec 25, 2017 7:31:36 PM","content":"# 移动启航班任务-个人移动名片\n\n## 任务描述：\n\n实现一个简单的网页，作为自己的移动名片\n\n以上图仅作参考，大家可以随意按照自己的喜欢进行界面设计。\n\n## 任务要求：\n\n* 个人完成，团队内部交流，互相评价代码\n* 做完后在班级群里分享\n\n## 任务时间\n\n7月18日 至 7月24日\n\n## 学习资料\n\n### HTML\n\n- [知乎上的一些Web基本概念介绍](http://www.zhihu.com/question/22689579)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n- [w3school html教程](http://w3school.com.cn/html/index.asp)\n- [MDN HTML](https://developer.mozilla.org/zh-CN/docs/Web/HTML/Introduction)\n\n### CSS\n\n- [MDN CSS](https://developer.mozilla.org/zh-CN/docs/Web/Guide/CSS/Getting_started)\n- [w3school css](http://w3school.com.cn/css/index.asp)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n\n### JavaScript\n\n- [MDN](https://developer.mozilla.org/zh-CN/docs/Web/JavaScript)\n- [慕课网JavaScript入门](http://www.imooc.com/learn/36)\n- [慕课网JavaScript教程](http://www.imooc.com/learn/10)\n- [w3school](http://www.w3school.com.cn/js/)\n- [Codecademy](http://www.codecademy.com/tracks/javascript)\n\n### 移动\n\n* [移动WEB开发入门](http://junmer.github.io/mobile-dev-get-started/)\n* [移动开发资源集合](https://github.com/jtyjty99999/mobileTech)\n* [The Mobile Web Handbook](http://quirksmode.org/mobilewebhandbook/)\n* [MobileWeb 适配总结](http://www.w3ctech.com/topic/979)\n* [移动前端不得不了解的html5 head 头标签](http://www.css88.com/archives/5480)\n* [浅谈移动前端的最佳实践](http://www.cnblogs.com/yexiaochai/p/4219523.html)\n* [touch系事件](http://www.html-js.com/article/All-right-thinking-touch-events)\n* [移动端真机调试终极利器-BrowserSync](http://www.codingserf.com/index.php/2015/03/browsersync/)"},{"id":6,"title":"移动启航班任务-漂亮宣传手册","datetime":"Dec 25, 2017 7:31:36 PM","content":"# 移动启航班任务-漂亮宣传手册\n\n## 任务描述：\n\n实现一个多页的个人（或其他）宣传页面，支持手指滑动翻页，有漂亮的动画效果\n\n以上图仅作参考，大家可以随意按照自己的喜欢进行界面设计。\n\n## 任务要求：\n\n* 团队集体完成\n* 支持手指滑动来翻页\n* 有良好的动画效果\n* 必须使用HTML、CSS、JavaScript编码进行完成，不可以使用所见即所得的在线编辑工具制作\n* 允许使用基础的JS库，如Zepto，Hammer等\n* 对于直接用Html5营销页面框架，我们不建议但不做强制限制\n\n## 任务时间\n\n7月27日 至 8月14日\n\n## 学习资料\n\n### HTML\n\n- [知乎上的一些Web基本概念介绍](http://www.zhihu.com/question/22689579)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n- [w3school html教程](http://w3school.com.cn/html/index.asp)\n- [MDN HTML](https://developer.mozilla.org/zh-CN/docs/Web/HTML/Introduction)\n\n### CSS\n\n- [MDN CSS](https://developer.mozilla.org/zh-CN/docs/Web/Guide/CSS/Getting_started)\n- [w3school css](http://w3school.com.cn/css/index.asp)\n- [慕课网HTML+CSS基础课程](http://www.imooc.com/learn/9)\n\n### JavaScript\n\n- [MDN](https://developer.mozilla.org/zh-CN/docs/Web/JavaScript)\n- [慕课网JavaScript入门](http://www.imooc.com/learn/36)\n- [慕课网JavaScript教程](http://www.imooc.com/learn/10)\n- [w3school](http://www.w3school.com.cn/js/)\n- [Codecademy](http://www.codecademy.com/tracks/javascript)\n\n### 移动\n\n* [移动WEB开发入门](http://junmer.github.io/mobile-dev-get-started/)\n* [移动开发资源集合](https://github.com/jtyjty99999/mobileTech)\n* [The Mobile Web Handbook](http://quirksmode.org/mobilewebhandbook/)\n* [MobileWeb 适配总结](http://www.w3ctech.com/topic/979)\n* [移动前端不得不了解的html5 head 头标签](http://www.css88.com/archives/5480)\n* [浅谈移动前端的最佳实践](http://www.cnblogs.com/yexiaochai/p/4219523.html)\n* [touch系事件](http://www.html-js.com/article/All-right-thinking-touch-events)\n* [移动端真机调试终极利器-BrowserSync](http://www.codingserf.com/index.php/2015/03/browsersync/)"}]

